<template>
  <div class="hello1">
    <div class="th">
       <h1>{{msg1}}</h1>
       <h2 style="font-size:10px">欢迎:{{name}}</h2>
    </div>
    <div class="th">
         <img  src="../assets/2.png"/>
         <div class="tr">
             <div class="ry"></div>
             <div class="xy">
               <div class="ta"></div>
               <div class="tb"></div>
             </div>
             
         </div>
               
    </div>
  </div>
</template>

<script>
import { setCookie,getCookie,delCookie } from '../assets/js/cookie.js'
export default {
  name: 'infor',
  data () {
   return {
      msg1: '记录查询',
      name:''   
    }
 
  },
  mounted(){
    let uname=getCookie('name')
    this.name=uname
    if(uname==''){
      this.$router.push('/')
    }
  },
  methods:{
     
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  font-weight: normal;
  color: white;
  margin-left:50%;
  font-size: 20px;
}
h2 {
  font-weight: normal;
  color: black;
  margin-left: 50px;
 margin-top: 20px;
}
 
 
.ta{
  width: 40%;
  height: 100%;
  border-right: 1px solid grey;
  
}
.tb{
  background-color: #acb4bd;
  width: 60%;
  height: 100%;

}
.ry{
  width: 100%;
  height: 15%;
  background-color: #90a2b5;
}
.xy{
  width: 100%;
  height: 90%;
  display: flex;;
  flex-direction: row;
}
.th{
  display: flex;
  flex-direction: row;
}
.tr{
  display: flex;
  flex-direction: column;
  border: 1px solid #6e889c;
  
  height: 200px;
  width: 350px;
  margin-left: 3%;
}


img{
margin-left:5%;
height:200px;
width: 100px;
border-radius: 10px;
}
.hello1{
  background-color:#4f4d4d;
  opacity: 0.7;
  height: 300px;
  width:500px;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);

}


</style>



