<template>

  <div class="hello">
       <h1>{{msg1}}</h1>
       <div class="th">
         <img  src="../assets/2.png"/>
         <div class="tr">
           <h2>{{msg2}}</h2> 
           <div class="tw">
             <button v-show="eva" style="position:absolute;left:35%;" @click="evaluate()">聊天记录分析</button>
             <button v-show="inf "style="position:absolute;left:70%;"@click="infor()">记录查询</button>
           </div>
           <button style="margin-left:32%;margin-top:25%;" @click="login()">点击登录</button>
         </div>
               
       </div>
  </div>  
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg1: 'Q悉———回忆最美,心动瞬间',
      msg2:'我没有很想你，只是在早上醒来时，看看有没有你发来信息和未接来电，我没有很想你，只是把你来电调成唯一的铃音，我没有很想你，只是在听歌时，被某句歌词击中，脑中出现短暂的空白，我没有很想你，只是想看看你的样子，听听你的声音，我又没有很想你，只是每次醒来时，第一个想到你……',
      eva:false,
      inf:false,
    }
  },
  //computed: {
     //msg2: function(){
     //   this.eva= this.$store.state.infor.eva;
      //  this.inf= this.$store.state.infor.inf;
    // }
      
      
    //},
  methods:{
     evaluate(){
         this.$router.push({path:'/evaluate'});
     },
     infor(){
         this.$router.push({path:'/infor'});

     },
     login(){
        this.$router.push({path:'/login'});
       //this.eva=1;
       //this.inf=1;
     }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  font-weight: normal;
  color: white;
  margin-left:25%;
  font-size: 20px;
}
h2{
margin-left:10%;
 font-weight: normal;
 color: white;
 font-size: 10px;
}
 button{
  border-radius: 5px;
  
  width: 140px;

 }

 button:hover
{ 
background-color:grey;
}
h2:hover{
  background-color:#bab3b3;
  }

.th{
  display: flex;
  flex-direction: row;
}
.tw{
  display: flex;
  flex-direction: row;
 
  padding-top: 20%;
}
.tr{
  display: flex;
  flex-direction: column;
}


img{
margin-left:5%;
height:200px;
width: 100px;
border-radius: 10px;
}
.hello{
  background-color:#4f4d4d;
  opacity: 0.7;
  height: 300px;
  width:500px;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);

}


</style>



